import React from "react"
import Layout from "../components/layout"

const style = {
    margin: '0 auto',
    textAlign: 'center',
    width: '60%'
}

export default () => (
    <Layout>
        <div style={style}>
        <h1>Page not found</h1>
        <h2>Whoops! It looks like this page doesn't exist... yet.</h2>
        </div>
    </Layout>
)